import React from 'react';
import { Table } from 'antd';
import {
  KEY_PROJECT_NAME,
  KEY_STATISTIC_ITEM,
  CATEGORIES,
  KEY_TICKET_TOTAL
} from '../constants';

function ProjectTable(props) {
  const projectColumn = (
    <Table.Column
      dataIndex={KEY_PROJECT_NAME}
      title={KEY_PROJECT_NAME}
      key={KEY_PROJECT_NAME}
      width={300}
    />
  );

  const otherColumns = [KEY_STATISTIC_ITEM,
    ...CATEGORIES, KEY_TICKET_TOTAL].map((item) => (
    <Table.Column
      dataIndex={item}
      title={item}
      key={item}
      width={300}
    />
  ));

  return (
    <Table
      title={(currentPageData) => { return props.title }}
      dataSource={props.filteredProjects.length > 0 ?
        props.data.filter(item => props.filteredProjects.indexOf(item[KEY_PROJECT_NAME]) !== -1) : props.data}
      pagination={{ pageSize: 50 }}
      scroll={{ y: 400 }}
      size='small'
    >
      {projectColumn}
      {otherColumns}
    </Table>
  );
}

ProjectTable.defaultProps = {
  filteredProjects: []
}

export default ProjectTable;
