import React from 'react';
import { Row, Col, Form, Select } from 'antd';
import ProjectTable from './ProjectTable';
import {
  STATISTIC_PERIOD_ADD,
  STATISTIC_BEFORE_PERIOD_REMAIN,
  STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_THIS_YEAR_TOTAL,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_BEFORE_PERIOD_NEED_HANDLE,
  STATISTIC_BEFORE_PERIOD_TOTAL,
  STATISTIC_PERIOD_RATE
} from '../constants';
import {
  formatToTable,
  formatToRateTable
} from '../utils';

class ProjectView extends React.Component {
  static defaultProps = {
    data: {}
  }

  constructor(props) {
    super(props);
    this.state = {
      filteredProjects: []
    }
  }

  filterProjects = (projects) => {
    if (projects.length > 0) {
      this.setState({
        filteredProjects: projects
      });
    } else {
      this.setState({
        filteredProjects: []
      });
    }
  }

  render() {
    let projects = [];
    for (let item in this.props.data) {
      for (let projectName in this.props.data[item]) {
        if (projects.indexOf(projectName) === -1) {
          projects = [...projects, projectName];
        }
      }
    }
    return (
      <div>
        <Form>
          <Row>
            <Col span={16}>
              <Form.Item
                label=''
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  mode="multiple"
                  style={{ width: '100%' }}
                  onChange={this.filterProjects}
                >
                  {
                    projects.map((project) => (
                      <Select.Option key={project}>{project}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_ADD}
              data={formatToTable(this.props.data.periodNewAddedResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.props.data.thisYearBeforePeriodResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.props.data.thisYearBeforePeriodTotalResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_THIS_YEAR_TOTAL}
              data={formatToTable(this.props.data.thisYearTotalResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.props.data.lastYearBeforePeriodResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.props.data.lastYearBeforePeriodTotalResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_NEED_HANDLE}
              data={formatToTable(this.props.data.beforePeriodNeedHandleResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_TOTAL}
              data={formatToTable(this.props.data.periodTotalResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_RATE}
              data={formatToRateTable(this.props.data.rateResult)}
              filteredProjects={this.state.filteredProjects}
            />
          </div>
        </Row>
      </div>
    );
  }
}

export default ProjectView;
