import {
  ADD_CATEGORY,
  ADD_CATEGORIES,
  ADD_TICKET_STATES
} from '../action-types';

export const addCategory = category => ({
  type: ADD_CATEGORY,
  payload: {
    category
  }
});

export const addCategories = categories => ({
  type: ADD_CATEGORIES,
  payload: {
    categories
  }
});

export const addTicketStates = ticketStates => ({
  type: ADD_TICKET_STATES,
  payload: {
    ticketStates
  }
})
