import React, { Component } from 'react';
import './App.css';
import DataForm from './components/DataForm';
import ChartView from './components/ChartView';
import ProjectView from './components/ProjectView';
import { Row, Col, message, LocaleProvider, Tabs } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import {
  TICKET_SYSTEM_COUNT,
  TICKET_ACCEPT,
  TICKET_OPEN,
  TICKET_VALIDATE,
  TICKET_PAUSE,
  TICKET_INVALID,
  TICKET_CLOSE,
  TICKET_CLOSE_NORMAL,
  TICKET_CLOSE_ABNORMAL,
  TICKET_CLOSE_ON_TIME,
  TICKET_RESPONSE_IN_30_MIN,
  TICKET_RESPONSE_IN_30_HOUR,
  TICKET_CLOSE_PROCESS_TIME,
  TICKET_OPEN_PROCESS_TIME,
  DEFAULT_STATISTIC_ITEMS,
  KEY_REPORT_TIME,
  KEY_PROJECT_NAME,
  KEY_SECOND_CATEGORY,
  KEY_TICKET_STATUS,
  KEY_IS_PROCESS_EXCCED,
  KEY_FIRST_RESPONSE_TIME,
  KEY_PROCESS_TIME,
  KEY_CLOSE_TIME,
  DEFAULT_SECOND_CATEGORIES,
  KEY_STATISTIC_ITEM,
  KEY_TICKET_TOTAL,
  KEY_CITY_NAME,
  NEW_ADD_CLOSE_RATE,
  REMAIN_CLOSE_RATE,
  OVERALL_CLOSE_RATE,
  RESPONSE_IN_30_MIN_RATE,
  RESPONSE_IN_30_HOUR_RATE,
  CLOSE_ON_TIME_RATE,
  TOTAL_OVERALL_CLOSE_RATE,
  RATE_STATISTIC_ITEMS,
  CATEGORY_UNCLASSIFY,
  COL_FIRST_CLASS,
  COL_CURRENT_STATUS,
  COL_PROJECT_STAGE,
  COL_PROJECT,
  COL_CITY_COMPANY,
  COL_AREA,
  COL_REPORT_TIME,
  CATEGORIES
} from './constants';

import {
  filterProjectName,
  parseTime,
  filterProjectStage,
  formatToAreaTable,
  formatToAreaTateTable,
  readExcelData
} from './utils';

import { connect } from 'react-redux';
import { addCategories, addTicketStates } from './store/actions';

import * as Util from './functions/utils';

// global variables
let allCategory = [];
let allTicketState = [];

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stageGraphData: {},
      cityGraphData: {},
      projectGraphData: {},
      areaGraphData: {},
      categoryGraphData: {}
    }
  }

  start = (fileList, startTime, endTime) => {
    const { addCategories, addTicketStates } = this.props;
    this.setState({ loading: true });
    readExcelData(fileList).then((data) => {
      let allStages = [], allProjects = [], allCities = [], allAreas = [];
      // get all categories and ticket states
      for (let ticket of data) {
        let category = Util.getCategory(ticket);
        if (allCategory.indexOf(category) === -1) {
          allCategory = [...allCategory, category];
        }
        let state = ticket[COL_CURRENT_STATUS];
        if (allTicketState.indexOf(state) === -1) {
          allTicketState = [...allTicketState, state];
        }

        // get all stages
        let stage = ticket[COL_PROJECT_STAGE];
        if (allStages.indexOf(stage) === -1) {
          allStages = [
            ...allStages,
            stage
          ];
        }
        // get all projects
        let project = ticket[COL_PROJECT];
        if (allProjects.indexOf(project) === -1) {
          allProjects = [
            ...allProjects,
            project
          ];
        }
        // get all cities
        let city = ticket[COL_CITY_COMPANY];
        if (allCities.indexOf(city) === -1) {
          allCities = [
            ...allCities,
            city
          ];
        }
        // get all area
        let area = ticket[COL_AREA];
        if (allAreas.indexOf(area) === -1) {
          allAreas = [
            ...allAreas,
            area
          ];
        }
      }
      addCategories(allCategory);
      addTicketStates(allTicketState);

      let allCategories = CATEGORIES;

      this.setState({
        allStages,
        allProjects,
        allCities,
        allAreas,
        allCategories
      });

      let yearStartTime = moment(startTime).set({ month: 0, date: 1, hour: 0, minute: 0, second: 0 });
      let periodTickets = data.filter(ticket => {
        let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
        return ticketReportTime >= startTime && ticketReportTime <= endTime;
      });
      let beforePeriodTickets = data.filter(ticket => {
        let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
        return ticketReportTime >= yearStartTime && ticketReportTime < startTime;
      });
      let thisYearTotalTickets = data.filter((ticket) => {
        let reportTime = moment(ticket[KEY_REPORT_TIME]);
        return reportTime <= endTime && reportTime >= yearStartTime;
      });
      let lastYearBeforePeriodTickets = data.filter(ticket => {
        let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
        return ticketReportTime < yearStartTime
      });

      // 
      let stageData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
        lastYearBeforePeriodTickets, startTime, endTime, COL_PROJECT_STAGE, allStages)
      this.setState({
        stageData
      });

      // 
      let projectData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
        lastYearBeforePeriodTickets, startTime, endTime, COL_PROJECT, allProjects)
      this.setState({
        projectData
      });

      // 
      let cityData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
        lastYearBeforePeriodTickets, startTime, endTime, COL_CITY_COMPANY, allCities)
      this.setState({
        cityData
      });

      // 
      let areaData = Util.statistc(periodTickets, beforePeriodTickets, thisYearTotalTickets,
        lastYearBeforePeriodTickets, startTime, endTime, COL_AREA, allAreas)
      this.setState({
        areaData
      });

      // 
      let stageGraphData = Util.formatGraphData(stageData, allStages);
      this.setState({
        stageGraphData
      })

      let projectGraphData = Util.formatGraphData(projectData, allProjects);
      this.setState({
        projectGraphData
      });

      let cityGraphData = Util.formatGraphData(cityData, allCities);
      this.setState({
        cityGraphData
      });

      let areaGraphData = Util.formatGraphData(areaData, allAreas);
      this.setState({
        areaGraphData
      });

      // 
      let categoryGraphData = Util.formatCategoryGraphData(areaData, allCategories, allAreas[0]);
      this.setState({
        categoryGraphData
      });


      this.setState({
        loading: false
      });
    });
  }

  render() {
    const tabChart = (
      <ChartView
        stageGraphData={this.state.stageGraphData}
        projectGraphData={this.state.projectGraphData}
        cityGraphData={this.state.cityGraphData}
        areaGraphData={this.state.areaGraphData}
        categoryGraphData={this.state.categoryGraphData}
        allStages={this.state.allStages}
        allProjects={this.state.allProjects}
        allCities={this.state.allCities}
        allAreas={this.state.allAreas}
        allCategories={this.state.allCategories}
      />
    );

    const tabArea = (
      <ProjectView
        data={this.state.areaData}
      />
    );

    const tabCity = (
      <ProjectView
        data={this.state.cityData}
      />
    )

    const tabProject = (
      <ProjectView
        data={this.state.projectData}
      />
    );

    const tabStage = (
      <ProjectView
        data={this.state.stageData}
      />
    );

    return (
      <LocaleProvider locale={zh_CN}>
        <div>
          <h2 className="text--center heading"></h2>
          <DataForm
            onStart={this.start}
            onExport={this.exportExcel}
            loading={this.state.loading}
          />
          <Row style={{ margin: '40px 0' }}>
            <Col span={22} offset={1}>
              <Tabs type="card">
                <Tabs.TabPane tab="" key="">{tabChart}</Tabs.TabPane>
                <Tabs.TabPane tab="" key="">{tabArea}</Tabs.TabPane>
                <Tabs.TabPane tab="" key="">{tabCity}</Tabs.TabPane>
                <Tabs.TabPane tab="" key="">{tabProject}</Tabs.TabPane>
                <Tabs.TabPane tab="" key="">{tabStage}</Tabs.TabPane>
              </Tabs>
            </Col>
          </Row>
        </div>
      </LocaleProvider>
    );
  }
}

const mapDispatchToProps = {
  addCategories,
  addTicketStates
}

const ConnectedApp = connect(null, mapDispatchToProps)(App);

export default ConnectedApp;
