export const statisticThisYearBeforePeriodTotalTickets = (tickets, closeStartTime, closeEndTime, col, allItems) => {
  let dataMap = {};
  for (let name of allItems) {
    dataMap[name] = initStatisticItem();
  }
  for (let ticket of tickets) {
    let name = ticket[col];
    if (dataMap[name] == null) {
      dataMap[name] = initStatisticItem();
    }
    let ticketCategory = getCategory(ticket);
    let ticketStatistic = dataMap[name][ticketCategory];
    let ticketStatus = ticket[COL_CURRENT_STATUS];
    let ticketCloseTime = moment(ticket[COL_CLOSE_TIME_1] || ticket[COL_CLOSE_TIME_2] ||
      ticket[COL_CLOSE_TIME_3]);

    //  = +++++++++++
    if (ticketStatus === TICKET_STATUS.RESPONSED || ticketStatus === TICKET_STATUS.SOLUTION_PREPARE ||
      ticketStatus === TICKET_STATUS.WORK_FINISHED ||
      ticketStatus === TICKET_STATUS.DROP_IN ||
      ticketStatus === TICKET_STATUS.SOLUTION_APPROVE ||
      ticketStatus === TICKET_STATUS.WORK_DOING ||
      ticketStatus === TICKET_STATUS.DISPATCHED || ticketStatus === TICKET_STATUS.NEW ||
      ticketStatus === TICKET_STATUS.CLOSED ||
      ticketStatus === TICKET_STATUS.FORCE_CLOSE || ticketStatus === TICKET_STATUS.CLOSE_ABNORMAL) {
      ticketStatistic[TICKET_ACCEPT]++;
    }
  }

  for (let name in dataMap) {
    for (let statisticItem of STATISTIC_ITEMS) {
      for (let category of CATEGORIES) {
        dataMap[name][KEY_TICKET_TOTAL][statisticItem] += dataMap[name][category][statisticItem];
      }
    }
  }

  return dataMap;
}

